.navbar {
  /* padding: 1.8rem 0; */
  box-shadow: rgba(0, 0, 0, 0.5) 0px 10px 15px -3px,
    rgba(0, 0, 0, 0.5) 0px 4px 6px -2px;
}

.navbar-brand {
  width: 90px;
}

.brand-and-toggler {
  width: 100%;
}

.navbar-content {
  display: flex;
  width: 100%;
}

.navbar-toggler-btn {
  transition: var(--transition);
  z-index: 11;
}

.navbar-collapse {
  position: fixed;
  right: 0;
  top: 0;
  z-index: 10;
  background-color: var(--grey);
  height: 100%;
  width: 280px;
  display: flex;
  padding: 10rem 3rem 0 3rem;
  transform: translateX(100%);
  transition: var(--transition);
}

.show-navbar-collapse {
  transform: translateX(0);
}

.nav-item {
  margin-bottom: 1.4rem;
}

.nav-link {
  transition: var(--transition);
}
.nav-link:hover {
  color: var(--blue);
}

@media (min-width: 992px) {
  .navbar-collapse {
    position: relative;
    height: auto;
    padding: 0;
    width: 100%;
    background-color: transparent;
    transform: translateX(0);
    justify-content: flex-end;
  }

  .navbar-toggler-btn {
    display: none;
  }

  .nav-item {
    margin-bottom: 0;
    margin-left: 2.8rem;
  }

  .nav-link {
    color: var(--brown);
    font-size: 1.7rem;
    font-weight: 500 !important;
  }

  .navbar-nav {
    display: flex;
  }
}
